import React, { Component } from 'react';

import appText from '../../data/appText';

import { withStyles } from '@material-ui/core/styles';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import Table from '@material-ui/core/Table';
import TableBody from '@material-ui/core/TableBody';
import TableRow from '@material-ui/core/TableRow';
import TableCell from '@material-ui/core/TableCell'
import TextField from '@material-ui/core/TextField';
import InputAdornment from '@material-ui/core/InputAdornment';
import CMI from '../../components/SheetColumns/CMI';
import CAI from '../../components/SheetColumns/CAI';
import Live from '../../components/SheetColumns/Live';

const styles = theme => ({
    root: {
        padding: theme.spacing.unit * 2,
        width: '100%'
    },
    paper: {
        padding: theme.spacing.unit * 2
    },
    tableCell: {
        borderBottom: 0
    },
    divider: {
        marginTop: theme.spacing.unit * 2,
        marginBottom: theme.spacing.unit * 2
    }
});

class CostCalculatorSheet extends Component {
    render() {
        const { classes } = this.props;
            
        return (
            <Grid container spacing={8} className={classes.root}>
                <Grid container item xs={12} spacing={0}>
                    <Grid item xs={4}>
                        <Table>
                            <TableBody>
                                <TableRow>
                                    <TableCell colSpan={2} className={classes.tableCell}>
                                        <Typography variant="h6" gutterBottom>Shared Fields</Typography>
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell className={classes.tableCell}>
                                        {appText.shared.fields.dailyClassroomCostPerLearner.label}
                                    </TableCell>
                                    <TableCell className={classes.tableCell}>
                                        <TextField 
                                            type="number" id="shared-learnerCostPerDay" fullWidth
                                            className={classes.textField}
                                            onChange={this.props.handleFieldChange} value={this.props.state.shared.learnerCostPerDay}
                                            InputProps={{
                                                startAdornment: <InputAdornment position="start">$</InputAdornment>
                                            }} />
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell className={classes.tableCell}>
                                        {appText.shared.fields.annualLoadedCostPerInst.label}
                                    </TableCell>
                                    <TableCell className={classes.tableCell}>
                                        <TextField 
                                            type="number" id="shared-loadedAnnualInstructorCost" fullWidth
                                            className={classes.textField}
                                            onChange={this.props.handleFieldChange} value={this.props.state.shared.loadedAnnualInstructorCost}
                                            InputProps={{
                                                startAdornment: <InputAdornment position="start">$</InputAdornment>
                                            }}/>
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell className={classes.tableCell}>
                                        {appText.shared.fields.dailyTDYCostPerLearner.label}
                                    </TableCell>
                                    <TableCell className={classes.tableCell}>
                                        <TextField 
                                            type="number" id="shared-TDYCostPerDay" fullWidth
                                            className={classes.textField}
                                            onChange={this.props.handleFieldChange} value={this.props.state.shared.TDYCostPerDay}
                                            InputProps={{
                                                startAdornment: <InputAdornment position="start">$</InputAdornment>
                                            }} />
                                    </TableCell>
                                </TableRow>
                                <TableRow>
                                    <TableCell className={classes.tableCell}>
                                        {appText.shared.fields.totalTargetAudienceThroughput.label}
                                    </TableCell>
                                    <TableCell className={classes.tableCell}>
                                        <TextField 
                                            type="number" id="shared-learnerTargetAudience" fullWidth
                                            className={classes.textField}
                                            onChange={this.props.handleFieldChange} value={this.props.state.shared.learnerTargetAudience} />
                                    </TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </Grid>
                </Grid>
                <Grid item xs={4}>
                    <CMI 
                        data={this.props.state}
                        handleIMILevelChange={this.props.handleIMILevelChange}
                        handleFieldChange={this.props.handleFieldChange}
                        handleTrainingType={this.props.handleTrainingType}/>
                </Grid>
                <Grid item xs={4}>
                    <CAI 
                        data={this.props.state}
                        handleIMILevelChange={this.props.handleIMILevelChange}
                        handleFieldChange={this.props.handleFieldChange}
                        handleTrainingType={this.props.handleTrainingType}/>

                </Grid>
                <Grid item xs={4}>
                    <Live 
                        data={this.props.state}
                        handleIMILevelChange={this.props.handleIMILevelChange}
                        handleFieldChange={this.props.handleFieldChange}
                        handleTrainingType={this.props.handleTrainingType}/>
                </Grid>
            </Grid>
        );
    }

}

export default withStyles(styles)(CostCalculatorSheet);